  -webkit-text-stroke: 0.5px #84aa95;
  font-size: 45px;
  font-family: "Allura";
  top: 47%;
}
@media screen and (max-width: 750px) {
  #top_page #happy_christmas {
    text-align: center;
    width: 100vw;
    font-size: 25px;
  }
}
