  margin-left: 15px;
  margin-right: 15px;
}
#top_page #icon_container span a {
  color: #d3d7dd;
  transition: 0.3s;
}
#top_page #icon_container span a:hover {
  transition: 0.3s;
}
#top_page #icon_container span #twitter:hover {
  color: #1da1f2;
}
#top_page #icon_container span #speakerdeck:hover {
  color: #118174;
}</style>
